.App {
  
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.canvas-form{
  display: flex;
  justify-content: left;
  
}
.canvas-container{
  border: 1px solid black;
  margin-top: -1px;
  margin-left: -1px;
  margin-bottom: -2px;
}

.variables{
  border: 1px solid black;
  margin-top: -1px;
  margin-left: -1px;
  margin-right: -1px;
  margin-bottom: -1px;
}

.time-label{
  text-align: center;
}
.time-value{
  text-align: center;
}
.labels{
  display: flex;
  justify-content: space-between;
  
}

.actual{
  display: flex;
  justify-content: left;

}
.inputed{
  display: flex;
  justify-content: left;

}

.graphs{
  border: 1px solid black;
  margin-top: -1px;
  
  margin-right: -1px;
  margin-bottom: -1px;
}